import React, { Component } from 'react';
import { connect } from 'react-redux';
import { todoLoad } from './_actions/todoActions';

class TodoControls extends Component {
  constructor(props) {
    super(props);
    this.loadTodo = this.loadTodo.bind(this);
  }

  loadTodo(e) {
    e.preventDefault();
    this.props.todoLoad();
  }

  render() {
    return (
      <button
        onClick={this.loadTodo}
        type="submit"
      >Load todo
      </button>
    );
  }
}

const mapStateToProps = state => ({

});

const mapDispatchToProps = dispatch => ({
  todoLoad: () => dispatch(todoLoad()),
});

export default connect(mapStateToProps, mapDispatchToProps)(TodoControls);



